.front {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    backface-visibility: hidden;
  }
  
  .front>img {
    width: 100%;
    height: 100%;
    border-radius: 5px;
  }
  
  .like {
    position: absolute;
    right: 0;
    bottom: 70px !important;
  }
  
  
  .front>span {
    position: absolute;
    display: block;
    color: white;
    background: rgb(230, 158, 24);
    width: 50px;
    height: 50px;
    padding: 13px;
    border-radius: 50%;
    text-align: center;
    right: -10px;
    top: -10px;
    backface-visibility: hidden;
    font-weight: 700;
  }
  
  .card-footer {
    padding: 15px;
    position: absolute;
    border-radius: 5px;
    bottom: 0;
    left: 0;
    right: 0;
    height: 120px;
    background: #1e1b26;
  }
  
  .card-footer>.trailer-btn,
  i {
    color: rgb(240, 98, 73);
    border-color: rgb(240, 98, 73);
  }
  
  .card-footer>i {
    position: absolute;
    bottom: 20px;
    right: 15px;
  }
  
  .card-footer>.trailer-btn {
    border: 1px solid;
    position: absolute;
    padding: 3px 7px;
    background: transparent;
    border-radius: 5px;
    bottom: 20px;
    left: 15px;
  }
  
  .card-footer>.trailer-btn:hover {
    text-decoration: none;
    cursor: pointer;
    background: rgb(240, 98, 73);
    border-color: rgb(240, 98, 73);
    color: white;
  }
  
  .card-footer>h4 {
    margin: 2px 0;
    text-align: left;
    font-size: 13px;
    font-weight: bold;
    color: white;
  }

  table{
      border-spacing: 3px;
  }

  th{
    text-align: center;
    font-size: 14;
    font-weight: bold;
    color: white
  }

  .card-footer>h5 {
    margin: 2px 0;
    text-align: left;
    font-size: 13px;
    font-weight: bold;
    color: white;
  }
  
  .card-footer>p {
    text-align: left;
    font-weight: 100;
    font-size: 11px;
    color: rgb(255, 255, 255);
  }
  .movies-grid {
    display: grid;
    grid-auto-flow: row;
    grid-template-columns: repeat(4, 1fr);
    grid-column-gap: 25px;
    justify-items: center;
    grid-row-gap: 20px;
  }
  
  @media only screen and (max-width: 1400px) {
    .movies-grid {
      grid-template-columns: repeat(3, 1fr);
    }
  }
  
  @media only screen and (max-width: 1092px) {
    .movies-grid {
      grid-template-columns: repeat(2, 1fr);
    }
  }
  
  
  @media only screen and (max-width: 620px) {
    .movies-grid {
      grid-template-columns: repeat(1, 1fr);
    }
  }